.btn {
    color: var(--white);
    font-weight: 600;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url("../../assets/img/circle-btn-bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 100px;
    overflow: hidden;
    background-color: transparent;
    border: none;
    cursor: pointer;

}

.btn:disabled {
    opacity: 0.6;
}

.btn:hover,
.btn:focus,
.btn:active {
    opacity: 0.8;
}

