html, body, #root {
    min-height: 100%;
}

.auth-body {
    background-image: linear-gradient(130.9deg, #281483 4.3%, #8F6ED5 60.86%, #D782D9 92.4%);
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
    height: auto;
    padding-bottom: 40px;
}

#root  {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}
.main-content {
    flex: 1 0 auto;
}
.main-footer {
    flex-shrink: 0;
}

.content {
    margin: 0 auto;
    width: 100%;
}

.content--footer {
    color: var(--white);
    padding: 20px 30px;
}

.content-fix {
    max-width: 1200px;
    padding: 0 20px;
    margin: 0 auto;
}

.content-body {
    max-width: 500px;
    padding: 20px;
    margin: 0 auto;
}

.tc {
    text-align: center;
    margin: 0 auto;
}

.auth-btn {
    text-align: center;
    display: flex;
    align-items: center;
    width: 100%;
    height: auto;
}

.auth-btn-content {
    font-size: 0.87rem;
    color: #5E72E4;
    white-space: normal;
}

.auth-btn-icon {
    width: 18px;
    margin-right: 15px;
}

.bubble {
    background-color: rgba(255, 255, 255, 0.4);
    display: inline-block;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    position: absolute;
}
.bubble-1 {
    opacity: 0.5;
    left: 44vw;
    top: 28vh;
    transform: translate(0, 0) scale(0.81);
}
.bubble-2 {
    opacity: 0.89;
    left: 2vw;
    top: 6vh;
    animation: move-2 30.04s infinite;
    transform: translate(0, 0) scale(1.87);
}
.bubble-3 {
    opacity: 0.39;
    left: 71vw;
    top: 83vh;
    animation: move-3 30.06s infinite;
    transform: translate(0, 0) scale(2.01);
}
.bubble-4 {
    opacity: 0.07;
    left: 81vw;
    top: 93vh;
    animation: move-4 30.08s infinite;
    transform: translate(0, 0) scale(1.22);
}
.bubble-5 {
    opacity: 0.53;
    left: 1vw;
    top: 97vh;
    animation: move-5 30.1s infinite;
    transform: translate(0, 0) scale(1.83);
}

@keyframes move-1 {
    48% {
        transform: translate(-96vw, -66vh);
    }
}
@keyframes move-2 {
    55% {
        transform: translate(-3vw, -29vh);
    }
}
@keyframes move-3 {
    44% {
        transform: translate(-17vw, -49vh);
    }
}
@keyframes move-4 {
    60% {
        transform: translate(-96vw, -87vh);
    }
}
@keyframes move-5 {
    54% {
        transform: translate(-47vw, -36vh);
    }
}

.loadingItem1 {
    bottom: 3px;
    animation: 0.5s ease-in slidein;
    animation-fill-mode: forwards;
}

.loadingItem2 {
    bottom: 59px;
    animation: 1s ease-in 0.5s slidein;
    animation-fill-mode: forwards;
}

.loadingItem3 {
    bottom: 115px;
    animation: 1.5s ease-in 1.5s slidein;
    animation-fill-mode: forwards;
}
@keyframes slidein {
    0% {
        left: calc(100%);
    }
    100% {
        left: 0;
    }
}

.slick-dots {
    bottom: auto;
    right: 0;
    top: 30px;
    display: flex !important;
    flex-direction: column;
     background: transparent;
    left: auto;
    width: auto;
    max-height: calc(100vh - 40px);
}

.slick-dots li {
    text-align: center;
}

.slick-vertical .slick-slide {
    border: none !important;
}

.slick-dots li button {
    width: 6px;
    height: 6px;
    border-radius: 100px;
    background: #fff;
    margin: 0 auto;
    opacity: 0.5;
}

.slick-dots .slick-active button {
    transform: scale(1.2);
    opacity: 1;
}

.slick-dots li button:before {
    content: none;
}

.button-clear-defaults {
    padding: 0;
    background: transparent;
    border: none;
}