.intro {
    font-family: 'Rock Salt';
    color: var(--white);
    position: fixed;
    font-size: 1.12rem;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0,0,0,0.8);
    z-index: 2;
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    padding: 10px;
}

.actions {
    text-align: center;
    width: 100%;
    max-width: 450px;

    margin-top: 60px;
}

.right {
    width: 110px;
    position: absolute;
    right: 10px;
    bottom: 50px;
}

.rightPointer {
    margin-left: auto;
}

.center {
    position: absolute;
    width: 130px;
    bottom: 20px;
    left: 50%;
    margin-left: -65px;
    text-align: center;
    cursor: pointer;
}

.circle {
    margin-top: 10px;
    width: 65px;
    margin-left: auto;
    margin-right: auto;
}

.circle img {
    max-width: 100%;
}