.body {
    background-image: url("../../assets/img/auth/auth_bg.jpg");
    background-repeat: no-repeat;
    min-height: 100vh;
    background-size: cover;
    background-position: 50% 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.body :global(.content-fix) {
    display: flex;
    flex: 1;
    flex-direction: column;
}

.logo {
    margin: 110px auto;
    width: 265px;
}

.logo img {
    width: 100%;
}

.footer {
    margin: auto auto 70px auto;
    text-align: center;
    max-width: 140px;
}

.footer button {
    margin: 0 auto;
}

.text {
    color: var(--white);
    font-size: 0.75rem;
}