.switch {
    display: inline-block;
    height: 34px;
    position: relative;
    width: 60px;
}

.switch input {
    display:none;
}

.slider {
    background-color: #ccc;
    bottom: 0;
    cursor: pointer;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: .4s;
}

.slider:before {
    background-color: #fff;
    bottom: 4px;
    content: "";
    height: 26px;
    left: 4px;
    position: absolute;
    transition: .4s;
    width: 26px;
}

input:checked + .slider {
    background-color: #407bec;
}

input:checked + .slider:before {
    transform: translateX(26px);
}

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

.checkbox-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.field .select,
.field select {
    width: 100%;
}

.success-refresh {
    padding: 10px 20px;
    background-color: #af79f5;
    color: var(--white);
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.textarea {
    border: 1px solid #e6e6e6;
    padding: 10px;
    font-size: 1rem;
    border-radius: 5px;
}

button:disabled {
    opacity: 0.6;
}